import {useEffect, useState} from 'react';
import axios from 'axios';
import { Table, Button, Spinner, Container, Navbar, Nav } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import logo from './img/logo.png'
import './App.css';

import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';
  
export default function Home(){
    const navigate = useNavigate();

    let first = false;

    const [pasien, setPasien] = useState([]);
    const [dokter, setDokter] = useState([]);

    const [loading1, setLoading1] = useState([false]);

    const url1 = '//103.226.139.31:6003/getpasien';
    const url2 = '//103.226.139.31:6003/getdokter';

    function HandleError(){
        navigate('/servererror');
    }

    useEffect(() => {
    if(!first){
        first = true;
        let getPasien = axios.get(url1+`?num=${Math.round(Math.random() * 1000)}`, {headers: {'Cache-Control': 'no-cache','Pragma': 'no-cache','Expires': '0'}});
        let getdokters = axios.get(url2+`?num=${Math.round(Math.random() * 1000)}`, {headers: {'Cache-Control': 'no-cache','Pragma': 'no-cache','Expires': '0'}});
        Promise.all([getPasien, getdokters])
        .then(values =>{
            let pasiens = values[0].data;
            const dokters = values[1].data;
    
            pasiens.map(v => ({...v, dokterName: 'null'}))
            pasiens.map((item,i)=>{
                let index = dokters.findIndex(x => x._id === item.idDokter);
                pasiens[i].dokterName = dokters[index].docterName;
                pasiens[i].no = i+1;
            })
            
            setPasien(pasiens);
            setDokter(dokters);
            setLoading1(false);
            // console.log('First Get Data')
        })
        .catch(err=>{
        setLoading1(true);
        HandleError();
        });
    }

    let interval = setInterval(() => {
        let getPasien = axios.get(url1+`?num=${Math.round(Math.random() * 1000)}`, {headers: {'Cache-Control': 'no-cache','Pragma': 'no-cache','Expires': '0'}});
        let getdokters = axios.get(url2+`?num=${Math.round(Math.random() * 1000)}`, {headers: {'Cache-Control': 'no-cache','Pragma': 'no-cache','Expires': '0'}});

        Promise.all([getPasien, getdokters])
        .then(values =>{

            let pasiens = values[0].data;
            const dokters = values[1].data;
    
            pasiens.map(function(item){item.dokterName = 'null';});
            pasiens.map(function(item){item.no = 0;});

            pasiens.map((item, i)=>{
                let index = dokters.findIndex(x => x._id === item.idDokter);
                pasiens[i].dokterName = dokters[index].docterName;
                pasiens[i].no = i+1;
                // console.log(pasiens[i].dokterName);
            })
            
            setPasien(pasiens);
            setDokter(dokters);
            setLoading1(false);
            // console.log('Get Data')
        })
        .catch(err=>{
        setLoading1(true);
        HandleError();
        });
        
    }, 5000);
    return () => {
        clearInterval(interval);  
    };
    },[])


    const columns = [
        // { dataField: 'no', text: 'No', sort: false},
        { dataField: 'name', text: 'Name', sort: true, style: { cursor:'pointer' }, headerStyle: { backgroundColor: '#009879', color:'#ffffff'} },
        { dataField: '_id', text: 'ID', sort: true,style: { cursor:'pointer' } , headerStyle: { backgroundColor: '#009879', color:'#ffffff' }},
        { dataField: 'ttl', text: 'Tanggal Lahir', sort: true,style: { cursor:'pointer' }, headerStyle: { backgroundColor: '#009879', color:'#ffffff' }},
        { dataField: 'address', text: 'Alamat', sort: true,style: { cursor:'pointer' }, headerStyle: { backgroundColor: '#009879', color:'#ffffff' }},
        { dataField: 'dokterName', text: 'DPJP', sort: true,style: { cursor:'pointer' }, headerStyle: { backgroundColor: '#009879', color:'#ffffff' }},
      ];

      const defaultSorted = [{
        dataField: 'name',
        order: 'asc'
      }];

      const HandleClick ={
        onClick: (event, row) => {
            event.preventDefault();
            // console.log(row)
            navigate('/detail', {state: { id: row._id, name: row.name, dokter:row.dokterName, ttl:row.ttl, alamat:row.address } });
        }
      };

      const pagination = paginationFactory({
        page: 1,
        sizePerPage: 10,
        lastPageText: '>>',
        firstPageText: '<<',
        nextPageText: '>',
        prePageText: '<',
        showTotal: false,
        alwaysShowAllBtns: true,
        hideSizePerPage: true,
        onPageChange: function (page, sizePerPage) {
        // console.log('page', page);
        // console.log('sizePerPage', sizePerPage);
        },
        onSizePerPageChange: function (page, sizePerPage) {
        // console.log('page', page);
        // console.log('sizePerPage', sizePerPage);
        }


    });

    const { SearchBar, ClearSearchButton } = Search;


    const searchProps = {
        srText:'',
        delay:50
    };
     
    const clearSearch = {
        className:'btn btn-close',
        text:'',
        display: "inline-block"
    };
    

    return (
        <div className='App'>
            <Navbar expand="lg" className="bg-body-tertiary" bg="dark" data-bs-theme="dark">
                <Container>
                <Navbar.Brand href="#">
                    <img
                    alt=""
                    src={logo}
                    width="80"
                    height="30"
                    className="d-inline-block align-top"
                    />{'   '}
                    MyoMES
                </Navbar.Brand>
                <Navbar.Toggle />
                    <Navbar.Collapse className="justify-content-end">
                        <Navbar.Text>
                           Beta Version
                        </Navbar.Text>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            <br />
            {!loading1 ? (
            <>
            <Container>
                <h3>Daftar Pasien Terapi Myomes</h3>
                <ToolkitProvider
                    keyField='_id'
                    data={pasien}
                    columns={columns}
                    search
                >
                {
                    props => (
                        <div >

                            <div style={{ float: "right" }}>
                                <SearchBar {...props.searchProps}  {...searchProps}/>
                                <ClearSearchButton { ...props.searchProps} {...clearSearch}/>
                            </div>
                            <br></br>               

                            <BootstrapTable {...props.baseProps}
                                bootstrap4 striped hover
                                bordered={false}
                                defaultSorted={defaultSorted} 
                                pagination={pagination}
                                rowEvents={HandleClick}
                            />
                        </div>
                    )
                }
                </ToolkitProvider>
            </Container>
            <br></br>
            <br></br>
            <br></br>
            </>
            ):
            <div className='App'>
                <div className='spinner'>
                    <Spinner animation='border' variant="success" />
                </div>
            </div> 
        }
        </div>
    );
}