import {useLocation} from 'react-router-dom';
import {useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Table, Button, Spinner, Modal, Container, Row, Col, Navbar } from 'react-bootstrap';
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";
import './App.css';
import paginationFactory from 'react-bootstrap-table2-paginator';
import logo from './img/logo.png'
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';
import BootstrapTable from 'react-bootstrap-table-next';
import './App.css';
import { ToastContainer, toast } from 'react-toastify';


let showingModal = false;

export default function Detail(){
    let id, name, dokter, ttl, alamat;
    let first = false;
    let lastData = [];
    
    let interval;

    const [loading, setLoading] = useState([true]);
    const [terapi, setTerapi] = useState([]);

    const [showAlert, setShowAlert] = useState(false);

    const {state} = useLocation();

    if(state){
        id= state.id; //Read values passed on state
        name = state.name;
        dokter = state.dokter;
        ttl = state.ttl;
        alamat = state.alamat;
    }
    else{
        id= ' - '; // Read values passed on state
        name = ' - '
        dokter = '-'
    }

    function FetchData(){
        axios.get(`//103.226.139.31:6003/getterapi/${id}?num=${Math.round(Math.random() * 1000)}`)
            .then(res =>{
                let terapi = res.data;
        
                terapi.map(v => ({...v, tanggalTerapi: 'null'}))
                terapi.map(v => ({...v, durasi: 0}))
                terapi.map(v => ({...v, no: 0}))
                terapi.map((item,i)=>{

                    const tanggalTerapi = printDate(new Date(item.createOnDevice),0);
                    const chxnum = item.chx.split(';').map(Number)
                    const chxMax = ((Math.max(...chxnum)) * 10/1000).toFixed(2);
                    terapi[i].durasi = chxMax;
                    terapi[i].tanggalTerapi = tanggalTerapi;
                    terapi[i].no = i+1;
                    // console.log(pasiens[i].dokterName);
                })

                setTerapi(terapi);
                setLoading(false);
                // console.log('First Get Terapi Data');

                if(terapi.length !== lastData.length){
                    // console.log('New Data');
                    lastData = terapi;
                    setShowAlert(true);
                }
                else{
                    // console.log('Old Data');
                    lastData = terapi;
                    setShowAlert(false);
                }

            })
            .catch(err=>{
                setLoading(true);
            }) 
    }

    useEffect(() => {
        if(!first){
            first = true;
            FetchData();
        }
            interval = setInterval(() => {
                if(!showingModal){
                    FetchData();
                }
            }, 5000);
            return () => {
                clearInterval(interval);
            };
    },[]);

    const [showModal, setShowModal] = useState(false);
    const [modalInfo, setModalInfo] = useState([]);

    const [show, setShow] = useState(false);
    const handleClose = () => {
        setShow(false);
        showingModal = false;
        // console.log(showingModal)
    }
    const handleShow = () => {
        setShow(true);
        showingModal = true;
        // console.log(showingModal)
    }

    const toggleTrueFalse = () => {
        setShowModal(handleShow);
    }

    function handleDownload(event, id,user){
        event.preventDefault();
        // http://103.226.139.31:6003/getreport?id=64d9921dda1cc56da2ca97d7&username=Pt03
        // console.log('Download Triggered');
        const url = `http://103.226.139.31:6003/getreport?id=${id}&username=${user}`;
        // window.location.replace(url);
        window.open(url, "_blank", "noreferrer");

    }


    const ModalContent = () => {
        const tanggalTerapiModal = printDate(new Date(modalInfo.createOnDevice),0);
        let ch1 = modalInfo.ch1.split(';').map(Number);
        let ch2 = modalInfo.ch2.split(';').map(Number);
        let chx = modalInfo.chx.split(';').map(Number);
        ch1.shift();
        ch2.shift();
        chx.shift();

        const ch1Max = Math.max(...ch1);
        const ch1Min = Math.min(...ch1);
        const ch1Sum = ch1.reduce((a, b) => a + b, 0);
        const ch1Average = (Math.round(ch1Sum / ch1.length * 100) / 100).toFixed(2);

        const ch2Max = Math.max(...ch2);
        const ch2Min = Math.min(...ch2);
        const ch2Sum = ch2.reduce((a, b) => a + b, 0);
        const ch2Average = (Math.round(ch2Sum / ch2.length * 100) / 100).toFixed(2);

        const chxMax = ((Math.max(...chx)) * 10/1000).toFixed(2);


        const LineData = {
            labels: chx,
            datasets: [{
                label: 'CH1',
                data: ch1,
                fill: false,
                borderColor: '#673AB7',
                backgroundColor: '#673AB7',
                tension: 0.3
            },
            {
                label: 'CH2',
                data: ch2,
                fill: false,
                borderColor: '#EE5A24',
                backgroundColor: '#EE5A24',
                tension: 0.3
            }
        ]
        }

        const LineOptions = {
            maintainAspectRatio : false,
            scales: {
                y: {
                    beginAtZero: true,
                    ticks: {
                        font:{
                            size: 20,
                        },
                        fontColor: "#000000",
                        display:true
                    },
                    grid:{
                        color: '#000000'
                    },
    
                    title:{
                        display:true,
                        text:'Power',
                        font: {
                            size: 20,
                            weight: "bold",
                        }
                    }
                },
                x: {
                    beginAtZero: true,
                    ticks: {
                        font:{
                            size: 20,
                            fontColor: "#000000",
                        },
                        display:true
                    },
                    grid:{
                        color: '#000000'
                    },
                    title:{
                        display:true,
                        text:'Count',
                        font: {
                            size: 20,
                            weight: "bold",
                        },
                        fontColor: "#000000",
                    }
                }, 
            },
            elements: {
                point:{
                    radius: 0
                }
            },
            plugins: {
                legend: {
                    display: true,
                    position: "top",
                    align: "end",
                    labels:{
                        usePointStyle:true,
                        font:{
                            size:18
                        }
                    }
                }
            }
          };
        
        return(
            <Modal show={show} onHide = {handleClose} size="xl" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton >
                    <Modal.Title>Record Myomes - {modalInfo.name} - {tanggalTerapiModal}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Container>
                        <Row>
                            <Col xs={12} md={8} >
                                <div style={{ height:'400px', textAlign: "center" }}>
                                    <Line data={LineData} options = {LineOptions}/>
                                </div>
                            </Col>
                            <Col xs={8} md={4}>
                                <br></br>
                                <ul>
                                <table  size='sm'>
                                <tbody>
                                    <tr>
                                        <td width="30%">id</td>
                                        <td>: {modalInfo.idPasien}</td>
                                    </tr>
                                    <tr>
                                        <td>Nama</td>
                                        <td>: {modalInfo.name}</td>
                                    </tr>
                                    <tr>
                                        <td>Otot</td>
                                        <td>: {modalInfo.otot}</td>
                                    </tr>
                                    <tr>
                                        <td>Mode</td>
                                        <td>: {modalInfo.mode}</td>
                                    </tr>
                                    <tr>
                                        <td>DPJP</td>
                                        <td>: {dokter}</td>
                                    </tr>
                                    <tr>
                                        <td>Tanggal</td>
                                        <td>: {tanggalTerapiModal}</td>
                                    </tr>
                                </tbody>
                                </table>
                                </ul>
                                <br></br>
                                <ul>
                                    Durasi =  {chxMax} Detik
                                    <br></br>
                                    CH1 === Max: {ch1Max}, Min: {ch1Min}, Avg: {ch1Average} <br></br> CH2 === Max: {ch2Max}, Min: {ch2Min}, Avg: {ch2Average}
                                </ul>
                            </Col>
                        </Row>
                    </Container>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="success" onClick={event => {handleDownload(event, modalInfo._id, modalInfo.usernamePasien)}}>
                        Download Report
                    </Button>
                    <Button variant="danger" onClick={handleClose}>
                        Close
                    </Button>
                    
                </Modal.Footer>
            </Modal>
        )
    }


    function printDate(timestamp, timezone){
        const dateNow = new Date(timestamp + timezone*60*60*1000);
    
        var year = dateNow.getFullYear();
        var month = padTo2Digits(dateNow.getMonth() + 1);
        var day = padTo2Digits(dateNow.getDate());
        var hh = padTo2Digits(dateNow.getHours());
        var mm = padTo2Digits(dateNow.getMinutes());
        var ss = padTo2Digits(dateNow.getSeconds());
    
        const datePrint = year + '/' + month + '/' + day + ' - ' +hh + ':' + mm + ':' + ss;
    
        return datePrint;
    
    }

    function padTo2Digits(num) {
        return num.toString().padStart(2, '0');
    }


    const columns = [
        { dataField: 'no', text: 'No', sort: false, style: { cursor:'pointer'}, headerStyle: { backgroundColor: '#009879', color:'#ffffff', width:'60px'}},
        { dataField: 'tanggalTerapi', text: 'Tanggal Terapi', sort: true, style: { cursor:'pointer' }, headerStyle: { backgroundColor: '#009879', color:'#ffffff'} },
        { dataField: 'otot', text: 'Otot', sort: false,style: { cursor:'pointer' } , headerStyle: { backgroundColor: '#009879', color:'#ffffff'} },
        { dataField: 'mode', text: 'Mode', sort: false,style: { cursor:'pointer' }  , headerStyle: { backgroundColor: '#009879', color:'#ffffff'} },
        { dataField: 'durasi', text: 'Durasi (s)', sort: true,style: { cursor:'pointer' }  , headerStyle: { backgroundColor: '#009879', color:'#ffffff'},
            sortFunc: (a, b, order) => {
                const numA = parseFloat(a.replace(/\,/g,''), 10)
                const numB = parseFloat(b.replace(/\,/g,''), 10)
                if (order === "asc") {
                    return numB - numA;
                }
                return numA - numB;
            }
        },
      ];

      const defaultSorted = [{
        dataField: 'tanggal',
        order: 'asc'
      }];

      const HandleModal ={
        onClick: (event, item, dpjp) => {
            event.preventDefault();
            setModalInfo(item);
            toggleTrueFalse();
            
        }
      };

      const pagination = paginationFactory({
        page: 1,
        sizePerPage: 10,
        lastPageText: '>>',
        firstPageText: '<<',
        nextPageText: '>',
        prePageText: '<',
        showTotal: false,
        alwaysShowAllBtns: true,
        hideSizePerPage: true,
        onPageChange: function (page, sizePerPage) {
        // console.log('page', page);
        // console.log('sizePerPage', sizePerPage);
        },
        onSizePerPageChange: function (page, sizePerPage) {
        // console.log('page', page);
        // console.log('sizePerPage', sizePerPage);
        }


    });

    const { SearchBar, ClearSearchButton } = Search;
    // console.log(ClearSearchButton)

    const searchProps = {
        srText:'',
        delay:50
    };

    const clearSearch = {
        className:'btn btn-close',
        text:'',
        display: "inline-block"
    };

    const ttlday = ttl.split('-')[0];
    const ttlmonth = ttl.split('-')[1];
    const ttlyear = ttl.split('-')[2];

    const age = calculate_age(new Date(ttlyear, ttlmonth, ttlday));

    function calculate_age(dob) { 
        var diff_ms = Date.now() - dob.getTime();
        var age_dt = new Date(diff_ms); 
        
        return Math.abs(age_dt.getUTCFullYear() - 1970);
    }

    useEffect(() => {
        document.title = `MyoMes - Riwayat Terapi - ${name}`
        document.logo=logo
        }, []);
   
    
    const notify = () => {
        toast.success('New data has been received', {
            toastId: 'success1',
        })
    }
        
     
    if(showAlert){
        notify();
        setShowAlert(false);
    }

    return (
        <div>
            <Navbar expand="lg" className="bg-body-tertiary" bg="dark" data-bs-theme="dark">
                <Container>
                <Navbar.Brand >
                    <img
                    alt=""
                    src={logo}
                    width="80"
                    height="30"
                    className="d-inline-block align-top"
                    />{'   '}
                    MyoMES
                </Navbar.Brand>
                <Navbar.Toggle />
                    <Navbar.Collapse className="justify-content-end">
                        <Navbar.Text>
                           Beta Version
                        </Navbar.Text>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            <br />
        {!loading?(
            <>
            <Container>
            <ToastContainer
                position='top-right'
                autoClose={3000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme='light'
            />
            <h4>Riwayat Terapi Pasien</h4>
            <br></br>
            <Row>
            <Col></Col>
                <Col className="text-truncate" xs={1} style={{textAlign: "left" }}>
                Nama<br></br>
                id<br></br>
                </Col>
                <Col className="text-truncate" xs={4} style={{textAlign: "left" }}>
                : {name}<br></br>
                : {id}<br></br> 
                </Col>
                <Col className="text-truncate" xs={1} style={{textAlign: "left" }}>
                Umur<br></br>
                Alamat<br></br>
                </Col>
                <Col className="text-truncate" xs={4} style={{textAlign: "left" }}>
                : {age} tahun<br></br>
                : {alamat}<br></br>
                </Col>
            
                
            </Row>
            <ToolkitProvider
                    keyField='_id'
                    data={terapi}
                    columns={columns}
                    search
                >
                {
                    props => (
                        <div >

                            <div style={{ float: "right" }}>
                                <SearchBar {...props.searchProps}  {...searchProps}/>
                                <ClearSearchButton { ...props.searchProps} {...clearSearch}/>
                            </div>
                            <br></br>
                            
                            <BootstrapTable {...props.baseProps}
                                bootstrap4 striped hover 
                                bordered={false}
                                defaultSorted={defaultSorted} 
                                pagination={pagination}
                                rowEvents={HandleModal}  
                            />
                        </div>
                    )
                }
                </ToolkitProvider>
            {show ? <ModalContent/> : null}
            </Container>
            <br></br>
            <br></br>
            <br></br>
            </>
            ):(
            <>
                <div className='App'>
                    <div className='spinner'>
                        <Spinner animation='border' variant="success" />
                    </div>
                </div>
            </>
            )
        }
        </div>       
    )
}